import { useContext, useMemo } from 'react';
import TimeLineStateContext from '../TimelineStateContext';
import TimelineItemsStateContext from '../TimelineItemsStateContext';
import TimelineGroupsStateContext from '../TimelineGroupsStateContext';

export var useMinTime = function useMinTime() {
  var state = useContext(TimeLineStateContext);
  return state.minTime;
};

export var useMaxTime = function useMaxTime() {
  var state = useContext(TimeLineStateContext);
  return state.maxTime;
};

export var useViewStartTime = function useViewStartTime() {
  var state = useContext(TimeLineStateContext);
  return state.viewStartTime;
};

export var useViewEndTime = function useViewEndTime() {
  var state = useContext(TimeLineStateContext);
  return state.viewEndTime;
};

export var useMinViewDuration = function useMinViewDuration() {
  var state = useContext(TimeLineStateContext);
  return state.minViewDuration;
};

export var useMaxViewDuration = function useMaxViewDuration() {
  var state = useContext(TimeLineStateContext);
  return state.maxViewDuration;
};

export var useScheduleWidth = function useScheduleWidth() {
  var state = useContext(TimeLineStateContext);
  return state.scheduleWidth;
};

export var useGroupHeight = function useGroupHeight(id) {
  var state = useContext(TimeLineStateContext);
  var groupHeight = useMemo(function () {
    var groupHeightNullable = state.groupHeights.find(function (i) {
      return i.id === id;
    });
    return groupHeightNullable && groupHeightNullable.height ? groupHeightNullable.height : null;
  }, [state.groupHeights]);
  return groupHeight;
};

export var useItemHeights = function useItemHeights() {
  var state = useContext(TimeLineStateContext);
  return state.itemHeights;
};

export var useGroupIds = function useGroupIds() {
  var groups = useContext(TimelineGroupsStateContext);
  var groupIds = groups.map(function (g) {
    return g.id;
  });
  return groupIds;
};

export var useGroup = function useGroup(id) {
  var groups = useContext(TimelineGroupsStateContext);
  var group = useMemo(function () {
    return groups.find(function (g) {
      return g.id === id;
    });
  }, [id, groups]);
  return group;
};

export var useItems = function useItems() {
  var items = useContext(TimelineItemsStateContext);
  return items;
};

export var useItemIdsOfGroup = function useItemIdsOfGroup(groupId) {
  var items = useContext(TimelineItemsStateContext);
  var itemIdsOfGroup = useMemo(function () {
    return items.filter(function (i) {
      return i.groupId === groupId;
    }).sort(function (a, b) {
      return a.start - b.start;
    }) //relys on sorting on a copied array, i.e. the filter() in previous line
    .map(function (i) {
      return i.id;
    });
  }, [groupId, items]);
  return itemIdsOfGroup;
};

export var useItem = function useItem(id) {
  var items = useContext(TimelineItemsStateContext);
  var item = useMemo(function () {
    return items.find(function (i) {
      return i.id === id;
    });
  }, [id, items]);
  return item;
};