import { useMemo } from 'react';
import { useScheduleWidth, useMinTime, useMaxTime, useViewStartTime, useViewEndTime } from './useSelector';
import { differenceInMilliseconds, addMilliseconds, isAfter } from 'date-fns/fp';

export var useEntireScheduleWidth = function useEntireScheduleWidth() {
  var schedulerWidth = useScheduleWidth();
  var minTime = useMinTime();
  var maxTime = useMaxTime();
  var viewStartTime = useViewStartTime();
  var viewEndTime = useViewEndTime();

  var getMillisecondsFromMinTime = differenceInMilliseconds(minTime);
  var entireDuration = getMillisecondsFromMinTime(maxTime);

  var viewDuration = differenceInMilliseconds(viewStartTime)(viewEndTime);
  var entireSchedulerWidth = entireDuration / viewDuration * schedulerWidth;
  return entireSchedulerWidth;
};

export var useScheduleX = function useScheduleX() {
  var minTime = useMinTime();
  var viewStartTime = useViewStartTime();
  var viewEndTime = useViewEndTime();
  var scheduleWidth = useScheduleWidth();

  var getMillisecondsFromMinTime = differenceInMilliseconds(minTime);

  var viewDuration = differenceInMilliseconds(viewStartTime)(viewEndTime);

  var posX = scheduleWidth / viewDuration * -getMillisecondsFromMinTime(viewStartTime);
  return posX;
};

//#region conversions
export var useDurationToLengthFunc = function useDurationToLengthFunc() {
  var viewStart = useViewStartTime();
  var viewEnd = useViewEndTime();
  var scheduleWidth = useScheduleWidth();
  var duration = differenceInMilliseconds(viewStart)(viewEnd);
  var durationToLengthFunc = useMemo(function () {
    return function (milliseconds) {
      var length = milliseconds / duration * scheduleWidth;
      return length;
    };
  }, [duration, scheduleWidth]);
  return durationToLengthFunc;
};

export var useLeftToTimeFunc = function useLeftToTimeFunc() {
  var minTime = useMinTime();
  var maxTime = useMaxTime();
  var entireScheduleWidth = useEntireScheduleWidth();

  var leftToTimeFunc = useMemo(function () {
    return function (elementLeftX) {
      var getMillisecondsFromMinTime = differenceInMilliseconds(minTime);
      var entireDuration = getMillisecondsFromMinTime(maxTime);

      var millisecondsFrommMinTime = Math.max(elementLeftX / entireScheduleWidth * entireDuration, 0);

      var time = addMilliseconds(millisecondsFrommMinTime)(minTime);
      if (isAfter(maxTime)(time)) {
        time = maxTime;
      }
      return time;
    };
  }, [minTime, maxTime, entireScheduleWidth]);
  return leftToTimeFunc;
};

export var useScheduleElementXFunc = function useScheduleElementXFunc() {
  var minTime = useMinTime();
  var maxTime = useMaxTime();
  var entireScheduleWidth = useEntireScheduleWidth();

  var scheduleElementXfunc = useMemo(function () {
    return function (time) {
      var getMillisecondsFromMinTime = differenceInMilliseconds(minTime);
      var entireDuration = getMillisecondsFromMinTime(maxTime);

      var x = getMillisecondsFromMinTime(time) / entireDuration * entireScheduleWidth;
      return x;
    };
  }, [minTime, maxTime, entireScheduleWidth]);
  return scheduleElementXfunc;
};
//#endregion conversions